    };
  },
  props: {
    toggleVerticalMenuActive: {
      type: Function,
      default: () => {},
    },
  },
  mounted() {
    this.user = JSON.parse(localStorage.getItem("sw_user"));
  },
  methods: {
    logout() {
      localStorage.removeItem("sw_token");
      localStorage.removeItem("sw_user");
      this.$router.push({ name: "login" });
    },
  },
};
